
.room {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 97.8vh; /* Atur ketinggian sesuai kebutuhan */
}
  
.room_1 {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.room_1 h1 {
    margin:35px;
    margin-top: -20px;
}

.room_1 input {
    margin-bottom: 20px;
    width:20rem;
    height:35px;
    padding:5px;
}

.room_1 button {
    padding-left: 25px;
    padding-right: 25px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.button-1 {
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px 20px;
    background-color: rgb(245, 239, 239);
    color: black;
    border: none;
    cursor: pointer;
  }