* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.chat-app {
    background-color: red;
  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-height: 100vh;
    width: 100%;
    height: 100vh;
    background-color: #36393F;
  }

  .header {
    text-align: center;
    margin-top:10px;
    color: white;
    padding:23px;
  }

  .header p {
    cursor: pointer;
    color: yellow;
    text-decoration: underline;
  }
  
  .messages {
    width: 31em;
    height: 435px;
    overflow-y: scroll;
    overflow-x: hidden;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-top: 30px;
    margin-bottom: 5px;
    background-color: white;
  }

  .message {
    padding:10px;
  }
  
  .new-message-form {
    display: flex;
    justify-content: center;
    align-items: center;
    padding:50px;
  }
  
  .new-message-input {
    width: 20rem;
    height:40px;
    padding: 5px;
    margin-right: 10px;

  }
  
  .send-button {
    padding: 10px 20px;
  }

  .hilang {
    font-size: 0;
  }

  ::file-selector-button { font-size: initial; }
  
  .image-show {
    width: 450px;
    height: 400px;
  }

  .message-image.image-full-height {
    width: 450px;
    height: 400px;
  }
  
  .message-image.image-full-width {
    width: 450px;
    height: 400px;
  }
  
  .message-image.image-auto {
    width: 450px;
    height: 400px;
  }
  
  .message-image.image-custom-size {
    width: 450px;
    height: 400px;
  }
  
  
  .message a {
    color: #007bff;
    text-decoration: underline;
  }

  @media (max-width: 800px) {
    .container {
      height: 100vh;
    }
  
    .messages {
      width: 100%;
      height: 60vh;
      margin: 10px 0;
    }
  
    .new-message-form {
      flex-direction: column;
      padding-top: 20px;
    }
  
    .new-message-input {
      width: 250px;
      margin-bottom: 10px;
    }

    .send-button {
      padding: 10px 40px;
      margin-top: 15px;
    }
  }